import divider from '@/components/divider.vue';
import uniIcon from '@/components/uni-icon/uni-icon.vue'
export default {
	components:{
		divider,
		uniIcon
	},
	data() {
		return {
			background:{
				backgroundColor: '#fff',
			},
			params: {
				year: true,
				month: true,
				day: true,
				hour: true,
				minute: true,
				second: false
			},
			show: false,
			goodsShow:false,
			liveTime:"请选择开播时间"
		}
	},
	methods: {
		selectTime(){
			this.show = true
		},
		// 选择时间
		chooseTime(res) {
			this.liveTime = res.year+"年" +res.month + "月" + res.day + "日"+res.hour+"时"+res.minute+"分";
		},
		chooseGoods(){
			this.goodsShow=true
		}
	}
}
